import React, { Component } from 'react';
import Head from 'next/head';
import { RichText } from 'prismic-reactjs';
import { linkResolver } from '../lib/helpers';
import { Query } from 'react-apollo';
import Newsletter from './Newsletter';
import ButtonDark from './styles/ButtonDark';
import Accordion from './Accordion';
import KeyInfo from './KeyInfo';
import { CafeWrapper } from './styles/sections/cafeStyles';
import Link from 'next/link';
import { LazyLoadImage, LazyLoadComponent } from 'react-lazy-load-image-component';


class Cafe extends Component {
  constructor(props) {
    super(props);
    this.state = {
      success: false,
      submitted: false
    };
  }

  handleChange = e => {
    const { name, value } = e.target;
    this.setState({ [name]: value });
    if (value.length < 6) {
    }
  };


  serializeFormData = (form) => {

	// Setup our serialized data
	var serialized = [];

	// Loop through each field in the form
	for (var i = 0; i < form.elements.length; i++) {

		var field = form.elements[i];

		// Don't serialize fields without a name, submits, buttons, file and reset inputs, and disabled fields
		if (!field.name || field.disabled || field.type === 'file' || field.type === 'reset' || field.type === 'submit' || field.type === 'button') continue;



		// If a multi-select, get all selections
		if (field.type === 'select-multiple') {
			for (var n = 0; n < field.options.length; n++) {
				if (!field.options[n].selected) continue;
				serialized.push(encodeURIComponent(field.name) + "=" + encodeURIComponent(field.options[n].value));
			}
		}

		// Convert field data to a query string
		else if ((field.type !== 'checkbox' && field.type !== 'radio') || field.checked) {
      if (field.name == 'handle' && field.value != '') {
        var handle = field.value.toLowerCase();
        handle = 'https://www.instagram.com/' + handle;
        serialized.push(encodeURIComponent(field.name) + "=" + encodeURIComponent(handle));
      } else {
        serialized.push(encodeURIComponent(field.name) + "=" + encodeURIComponent(field.value));

      }
		}
	}

	return serialized.join('&');


  }

  sendToGoogleSheets = e => {
    e.preventDefault();

    var self = this;
    self.setState({ submitted: true });

    var url = 'https://script.google.com/macros/s/AKfycbyN94WC1RQ0HW8CBPiSNL2zCDI4P_Fesn6UM0JxD7rQ9WkWKO7C/exec';
    var params = this.serializeFormData(e.target);
    console.log(params);

    var http = new XMLHttpRequest();

    http.open("GET", url+"?"+params, true);
    http.onreadystatechange = function()
    {
        if (http.readyState == 4 && http.status == 200) {
            self.setState({ success: true })
        } else {
          self.setState({ submitted: false });
        }
    }
    http.send(null);
  };

  render() {
    const { pageData, newsletterData } = this.props;
    //
    var titleHtml = RichText.asText(pageData.main_heading, linkResolver);
    var subheadingHtml = RichText.asText(pageData.subheading, linkResolver);

    var introHeadingHtml = RichText.render(pageData.intro_heading, linkResolver);
    var introSubheadingHtml = RichText.render(pageData.intro_subheading, linkResolver);
    var introTextHtml = RichText.render(pageData.intro_text, linkResolver);
    var newsletterHeadingHtml = RichText.render(pageData.newsletter_heading, linkResolver);
    var newsletterSuccessHtml = RichText.asText(pageData.newsletter_success_message, linkResolver);
    var faqHeadingHtml = RichText.render(pageData.faq_heading, linkResolver);
    var faqNavTextHtml = RichText.asText(pageData.faq_nav_text, linkResolver);
    var menuNavTextHtml = RichText.asText(pageData.menu_nav_text, linkResolver);
    var navAddressLineHtml = RichText.asText(pageData.nav_address_line, linkResolver);
    var navLocationTextHtml = RichText.asText(pageData.nav_location_text, linkResolver);
    var pressHeadingHtml = RichText.render(pageData.press_heading, linkResolver);

    return (
      <div>
        <Head>
          {pageData.meta_description != null && (
            <title> {pageData.meta_title}</title>
          )}
          {pageData.meta_description != null && (
            <meta name="description" content={pageData.meta_description} />
          )}
          {pageData.keywords != null && (
            <meta name="keywords" content={pageData.keywords} />
          )}
          <link href="https://www.lowellherb.co/cafe" rel="canonical" />
        </Head>


        <CafeWrapper>
          <div className="subpage-link-wrapper">
            <Link href="/events">
              <a className="events-link">Private Events</a>
            </Link>
          </div>
          <div class="hide-mobile">
            <KeyInfo pageData={pageData}/>
          </div>
          <header>
          <LazyLoadImage src="static/collage/mobile/collage_2.png" className="mobile-collage show-mobile" effect="opacity" visibleByDefault="true"/>
            <div class="container-sm collage-parent">
              <div class="collage-images">
              <LazyLoadImage src="static/collage/desktop/collage_1.png" className="desktop-collage-1 hide-mobile" effect="opacity"/>
              <LazyLoadImage src="static/collage/desktop/collage_2.png" className="desktop-collage-2 hide-mobile" effect="opacity"/>
              <LazyLoadImage src="static/collage/desktop/collage_3.png" className="desktop-collage-3 hide-mobile" effect="opacity"/>
              </div>
              <div class="animation-wrapper">
                <h2>{ subheadingHtml }</h2>
                <h1>{ titleHtml }</h1>
              </div>
            </div>
          </header>
          <section class="prohibition">
            <div class="container-xs collage-parent">
              <div class="collage-images">
              <LazyLoadImage src="static/collage/desktop/collage_4.png" className="desktop-collage-1 hide-mobile" effect="opacity"/>
              <LazyLoadImage src="static/collage/desktop/collage_5.png" className="desktop-collage-2 hide-mobile" effect="opacity"/>
              </div>
              <hr />
              { introHeadingHtml }
              { introSubheadingHtml }
              { introTextHtml }
            </div>
          </section>

          <section>
            <div class="container-xs form-wrapper">
              <div class="collage-images">
                <LazyLoadImage src="static/collage/desktop/collage_6.png" className="desktop-collage-1 hide-mobile" effect="opacity"/>
                <LazyLoadImage src="static/collage/desktop/collage_7.png" className="desktop-collage-2 hide-mobile" effect="opacity"/>
                <LazyLoadImage src="static/collage/desktop/collage_8.png" className="desktop-collage-3 hide-mobile" effect="opacity"/>
              </div>
              <div class={`thanks ${this.state.success ? 'show-thanks': null}`}>
                <h2>{ newsletterSuccessHtml } </h2>
              </div>
              <h4 class="left-text">F<br/>o<br/>o<br/>d<br/> <span class="script"><img src="static/amp.png" width="22" alt="" /></span> <br/>C<br/>a<br/>n<br/>n<br/>a<br/>b<br/>i<br/>s</h4>
              <h4 class="right-text">E<br/>s<br/>p<br/>r<br/>e<br/>s<br/>s<br/>o<br/> <span class="script"><img src="static/amp.png" width="22" alt="" /></span> <br/>J<br/>u<br/>i<br/>c<br/>e</h4>
              <form id="lead-collection-form" onSubmit={this.sendToGoogleSheets} disabled={this.state.submitted}>
                <div class="validation-wrapper">
                  <label for="email">Email:</label>
                  <input type="email" id="email" name="email" placeholder="Email@gmail.com"  value={this.state.zipCode}
                  onChange={this.handleChange} required/>
                </div>
                <div class="validation-wrapper">
                  <label for="handle">Instagram:</label>
                  <div class="instagram-input-wrapper">
                    <input type="text" id="handle" name="handle" placeholder="yourhandle" value={this.state.zipCode}
                    onChange={this.handleChange} />
                  </div>
                </div>
                <div class="validation-wrapper">
                  <label for="month">Birthday:</label>
                  <input type="text" id="month" name="birth_month" placeholder="MM" class="inline-input" value={this.state.zipCode}
                  onChange={this.handleChange} />
                  <input type="text" id="day" name="birth_day" placeholder="DD" class="inline-input" value={this.state.zipCode}
                  onChange={this.handleChange} />
                </div>
                <div class="checkbox-wrapper">
                  <label for="email_opt_in"><input type="checkbox" onChange={this.handleChange} name="email_opt_in" id="email_opt_in" checked/> <span class="checkmark"><img src="static/check.svg" alt="" /></span> Send me info about the opening</label>
                  <label for="careers"><input type="checkbox" onChange={this.handleChange}name="careers" id="careers"/> <span class="checkmark"><img src="static/check.svg" alt="" /></span> I want to work here</label>
                </div>
                <button type="submit" disabled={this.state.submitted}>{`${this.state.submitted ? 'Sending...': 'Sign Up'}`}</button>
                <div className="events-link-wrapper">
                  <Link href="/events">
                  <a className="events-link">Host a Private Event</a>
                  </Link>
                </div>
              </form>
            </div>
          </section>
          <div class="show-mobile">
            <KeyInfo pageData={pageData}/>
          </div>
          <section class="container-md faq collage-parent">
            <div class="collage-images">
            <LazyLoadImage src="static/collage/desktop/collage_9.png" className="desktop-collage-1 hide-mobile" effect="opacity"/>
            <LazyLoadImage src="static/collage/desktop/collage_10.png" className="desktop-collage-2 hide-mobile" effect="opacity"/>
            <LazyLoadImage src="static/collage/desktop/collage_11.png" className="desktop-collage-3 hide-mobile" effect="opacity"/>
            <LazyLoadImage src="static/collage/desktop/collage_12.png" className="desktop-collage-4 hide-mobile" effect="opacity"/>
            <LazyLoadImage src="static/collage/desktop/collage_13.png" className="desktop-collage-5 hide-mobile" effect="opacity"/>
            <LazyLoadImage src="static/collage/desktop/collage_14.png" className="desktop-collage-6 hide-mobile" effect="opacity"/>
            </div>
            <hr class="show-mobile" />
            <LazyLoadImage src="static/collage/mobile/collage_1.png" className="mobile-collage show-mobile" effect="opacity" visibleByDefault="true"/>
            { faqHeadingHtml }
            <ul class="accordion">


            {pageData.faq_item.map((value, index) => {
              return (
                <LazyLoadComponent>
                  <Accordion faqItem={value}/>
                </LazyLoadComponent>
              )
            })}
          </ul>
          </section>
          <section class="press">
            { pressHeadingHtml }
            <div class="container collage-parent">
              <div class="collage-images">
                <LazyLoadImage src="static/collage/desktop/collage_15.png" className="desktop-collage-1 hide-mobile" effect="opacity"/>
              </div>

              {pageData.press_items.map((value, index) => {
                return (
                  <a href={value.press_link.url} target="_blank">
                    <LazyLoadImage src={value.press_image.url} className="press-logo" effect="opacity"/>
                  </a>
                )
              })}


            </div>
          </section>
        </CafeWrapper>

        <Newsletter newsletterData={newsletterData} page="cafe" />

      </div>
    );
  }
}

export default Cafe;
